/**
 * timeline.js
 * @author jason
 * timeline 时间线 在规定时间内完成一个涵数调用
 * @createAt 2016-06-10
 * var pauseOn = true;
 *      timeline( 1000, function( rate, animater ){
 *        if( animater.state == 'ended' && pauseOn ){
 *            pauseOn = false;//加来回一次，不加无限循
 *            animater.reverse();
 *          }
 *          var pos = compass( hook.pos, hook.a[ 0 ] + rate * hook.a[ 1 ], 50 )
 *          box.style.left = pos[ 0 ] + 'px';
 *          box.style.top = pos[ 1 ] + 'px';
 *        } );
*/
const timeline = function( time, callback ){
  var startTime = Date.now();
  let started,ended,restarts,stop,pauseOnOff,reverses,roundtrips,currentRate;
  var pauseTime,offSetTime = 0;
  let animater = {
    state: 'started',

    stop(){
      stop = true;
    },

    reverse(){
      reverses = !reverses;
      offSetTime = 0;
      init();
    },

    restart(){
      restarts = true;
      init();
    },

    pause(){
      pauseOnOff = true;
      pauseTime = Date.now();
    },

    resume(){
      pauseOnOff = false;
      offSetTime = Date.now() - pauseTime; 
    }
  };
  
  function init(){
    animater.state = 'started';
    startTime = Date.now();
  }

  let mainloop = function mainloop(){
    let nowTime,rate;

    if( pauseOnOff ){
      requestAnimationFrame( mainloop );
      return;
    }

    nowTime = Date.now();
    rate = ( nowTime - startTime - offSetTime ) / time;
    if( stop ){
      return;
    }
    
    if( animater.state == 'started' && rate < 1 ){
      callback( reverses ? 1 - rate : rate, animater );
    }else if( rate <= 0 ){
      rate = 0;
      callback( reverses ? 1 - rate : rate, animater );
    }else if( rate >= 1 ){
      animater.state = 'ended';
      rate = 1;
      callback( reverses ? 1 - rate : rate, animater );
    }

      requestAnimationFrame( mainloop );
  }();
  
};

export default timeline;