<template>
  <div class="map">
    <div class="header">
      <div class="controls">
        <span 
          @click="clickMagic" 
          :class="{ 
            active: active === 'magic',
            disabled: endBlock
          }" 
          class="minimal-path-button">魔法棒</span>
        <span
          @click="handlePause"
          :class="{ 
            active: active === 'pause',
            disabled: !endBlock
          }" 
          class="stop-button">
          {{ isPause ? '暂停' : '继续' }} 
        </span>
      </div>
      <div 
        class="info"
        :style="{
          width: rowNum * G + left * 2 + 'px'          
        }">
        <span style="padding-left: 1rem;">start: {{ start }}</span>
        <span style="padding-left: 1rem;">end: {{ end }}</span>
      </div>
    </div>
    <div 
      class="content" 
      @click="clickContent" 
      :style="{
        width: rowNum * G + left * 2 + 'px',
        height: rowNum * G + top + 'px'
      }">
      <div 
        v-for="item, index in modelData" 
        :key="index"
        class="grid"
        :class="{
          end: item.type === 'end',
          roadblock: item.type === 'roadblock',
          passby: item.type === 'passby'
        }"
        :data-index="item.index"
        :data-type="item.type"
        :style="{
          width: item.width + 'px',
          height: item.height + 'px',
          top: item.y + 'px',
          left: item.x + 'px'
        }">
        <div v-if="showFGH" :data-index="item.index" :data-type="item.type">
          {{ item.index }}
          <span style="color: #f02e33;">{{ item.F }}</span>
          {{ item.G }}
          {{ item.H }}
        </div>
      </div>
      <div 
        class="elf-box" 
        v-if="elfBlock"
        :style="{
          width: elfBlock.width + 'px',
          height: elfBlock.height + 'px',
          top: elfBlock.y + 'px',
          left: elfBlock.x + 'px'
        }"
        >
        <div class="elf"></div>
      </div>
    </div>
    <div class="modal-dom" v-if="showModal">
      <div class="mask"></div>
      <div class="wrap">
        <div class="modal-content">
          <div class="modal-title">设置</div>
          <div class="modal-body">
            <div>
              <div>宽高:</div> 
              <input type="number" v-model="setG" :min="2" :max="100" /></div>
            <div>
              <div>行列数:</div> 
              <input type="number" v-model="setRowNum" :min="5" :max="1000" />
            </div>
            <div>
              <div>FGH:</div>
              <select v-model="setShowFGH">
                <option :value="true">是</option>
                <option :value="false">否</option>
              </select>
            </div>
          </div>  
          <div class="modal-footer">
            <div @click="showModal = false">取消</div>
            <div @click="ok">确认</div>
          </div>    
        </div>
      </div>
    </div>
  </div>
</template>
<script>
const isMobile = /mobile/gi.test( navigator.userAgent );
import timeline from "../assets/js/timeline.js";
import clone from "../assets/js/clone.js";

export default {
  name: 'm-map',
  data(){
    return {
      modelData: [],
      modelMap: new Map,
      openList: [],
      start: 1,
      end: null,
      endBlock: null,
      startBlock: null,
      elfBlock: null,
      date: null,
      timing: 0,
      active: null,
      // roadblock: [ 4, 5, 8, 12, 18, 19, 22, 23, 24, 26, 29, 34, 36, 37, 39, 44, 46, 47, 49, 54, 56, 57, 59, 62, 63, 64, 67, 69, 75, 79, 81, 82, 83, 85, 87, 89, 93, 95 ],
      roadblock: isMobile ? [ 12, 14, 17, 19, 32, 34, 37, 39, 62, 63, 64, 65, 66, 67, 68, 69, 75, 76, 85, 86 ] : [2,3,13,18,20,27,28,29,30,31,32,37,42,47,54,55,59,64,67,69,71,76,78,83,85,87,89,91,94,95,99,105,107,110,117,121,125,126,127,131,136,138,140,147,153,161,163,164,165,168,169,170,175,176,177,178,179,181,183,186,191,194,199,207,213,216,217,219,226,227,230,233,235,237,239,243,248,250,252,255,257,259,262,268,270,274,279,283,285,286,290,292,295,297,299,301,304,306,307,308,310,313,315,317,319,324,326,328,330,333,336,339,343,344,351,354,359,361,366,367,369,370,372,375,376,377,378,379,383],
      left: 10,
      top: 0,
      G: isMobile ? 30 : 30,
      setG: isMobile ? 30 : 60,
      rowNum: isMobile ? 10 : 20,
      setRowNum: 10,
      showFGH: false,
      showModal: false,
      setShowFGH: false,
      speed: 200,
      wantGo: [],
      FGHBlocks: [],
      repeat: false,
      pause: null,
      resume: null,
      isPause: true
    }
  },

  mounted(){
    this.drawMap();
  },

  methods: {
    randomNum( min, max ){
      return Math.floor( Math.random() * ( max - min ) + min );
    },

    handlePause(e){
      if( !this.endBlock ) return;
      if( this.isPause ){
        this.isPause = false;
        this.pause();
      }else{
        this.resume();
        this.isPause = true;
      }
    },

    clickMagic(){
      if( this.endBlock ) return;
      if( this.active === 'magic' ){
        this.active = '';
      }else{
        this.active = 'magic';  
      }
    },

    clickContent( e ){
      let { dataset } = e.target;
      if( this.active === 'magic' && dataset.type !== 'start' ){
        let index = Number( dataset.index );
        let block = this.modelMap.get( index );
        if( !block.type )
          block.type = 'roadblock';
        else
          block.type = '';  

        this.$set( this.modelData, index - 1, block );
      }else if( dataset && dataset.index && dataset.type !== 'start' && dataset.type !== 'roadblock' && dataset.type !== 'end' ){
        let index = Number( dataset.index );
        let endBlock = this.modelMap.get( index );
        if( !this.endBlock ){
          endBlock.type = 'end';
          this.endBlock = endBlock;
          this.end = index
          this.minimalPath( this.startBlock, endBlock );
        }else{
          if( !this.repeat ){
            endBlock.type = 'end';
            this.repeat = true;
            this.end = index
            this.endBlock = endBlock;
          }
        }
      }
    },

    drawMap(){
      let modelData = this.compurGrid();
      for( let num of this.roadblock ){
        this.modelMap.get( num ).type = 'roadblock';
      }
      this.startBlock = this.modelMap.get( this.start );
      this.startBlock.type = 'start';
      this.elfBlock = clone( this.startBlock );
      this.modelData = modelData;
    },

    compurGrid(){
      let { left, top, rowNum, G } = this;
      let modelData = [];
      for( let y = 0; y < rowNum; y ++ ){
        for( let x = 0; x < rowNum; x++ ){
          let index = x + 1 + y * rowNum;
          let obj = {
            width: G, 
            height: G,
            x: x * G + left,
            y: y * G + top,
            index,
            direction: [ 
              index - rowNum, 
              index % rowNum === 0 ? 0 : index + 1, 
              index + rowNum, 
              index % rowNum === 1 ? 0 : index - 1
            ],//N, W, S, E
          }
          modelData.push( obj );
          this.modelMap.set( obj.index, obj )
        }
      }
      return modelData;
    },

    initData(){
      this.end = null;
      this.timing = null;
      this.modelData = [];
      this.modelMap.clear();
    },

    updateMap(){
      this.active = 'update-map';
      this.initData();
      let modelData = this.compurGrid();
      let roadblock = new Array( this.rowNum * (this.rowNum - Math.floor( this.rowNum / 2 )) );
      let i = 0;
      for( let num of roadblock )
        this.modelMap.get( this.randomNum( 1, this.rowNum * this.rowNum ) ).type = 'roadblock';

      this.modelData = modelData;
      this.active = null;
    },

    handlePath(){
      this.date = Date.now();
      this.minimalPath( this.startBlock );
    },

    minimalPath( startBlock, endBlock ){
      if( startBlock.type !== 'start' && endBlock.type !== 'end' ) return;
      this.pathScoring( startBlock, endBlock );
      let item;
      while( this.openList.length ){
        item = this.openList.pop();
        if( item.type === 'end' ){
          this.openList = [];
        }else{
          this.pathScoring( item, endBlock );
        }
      }
      
      if( item && item.type === 'end' ){
        this.drawPath( item );
        this.$forceUpdate();
      }else{
        alert('无法到达终点!');
        this.clearPath();
        this.endBlock.type = null;
        this.endBlock = null;
      }  
    },

    clearTypeOpenList(){
      this.FGHBlocks = this.FGHBlocks.filter( item => {
        if( item.type === 'openList' ){
          item.F = null;
          item.H = null;
          item.G = null;
          item.type = null;
          item.parent = null;
        }
        return item.type === 'start' || item.type === 'end' || item.type === 'passby';
      } )
    },

    drawPath( block ){
      if( block.type !== 'end' && block.type !== 'start' ) block.type = 'passby';
      if( block.parent ){
        this.wantGo.unshift( block );
        this.drawPath( this.modelMap.get( block.parent ) );
      }else{
        this.wantGo.unshift( block );
        this.clearTypeOpenList();
        this.goEnd();  
      }
    },

    goEnd(){
      this.repeat = false;
      let endBlock = this.endBlock;
      let X = 0, Y = 0;
      let startBlock, nextBlock;
      timeline( this.speed, ( rate, animater ) => {
        this.resume = animater.resume;
        this.pause = animater.pause;
        if( rate === 0 ){
          startBlock = this.wantGo.shift();
          startBlock.parent = null;
          startBlock.path = null;
          if( this.wantGo.length && endBlock.index === this.endBlock.index ){
            startBlock.type = null;
            nextBlock = this.wantGo[0];
            nextBlock.type = 'start';
            nextBlock.parent = null;
            nextBlock.path = null;
            this.startBlock = nextBlock;
            this.start = nextBlock.index;
          }else{
            animater.stop();
            this.pause = null;
            this.resume = null;
            this.clearPath();
            if( endBlock.type === 'start' ){
              if( endBlock.index === this.endBlock.index ){
                this.endBlock = null;
              }else{
                this.minimalPath( this.startBlock, this.endBlock );
              }
            }else{
              endBlock.type = null;
              endBlock.parent = null;
              this.endBlock.parent = null; 
              this.minimalPath( this.startBlock, this.endBlock );
            }
            return;
          }
        }        
        X = ( nextBlock.x - this.elfBlock.x ) * rate;
        Y = ( nextBlock.y - this.elfBlock.y ) * rate;
        this.elfBlock.x = this.elfBlock.x + X;
        this.elfBlock.y = this.elfBlock.y + Y;
        if( animater.state == 'ended'){
          animater.reverse()
        }
      } )
    },

    clearPath(){
      for( const item of this.FGHBlocks ){
        if( item.type !== 'start' && item.type !== 'end' ){
          item.type = null;
          item.parent = null;
          item.path = null;
          item.F = null;
          item.G = null;
          item.H = null;
        }
      }
      this.wantGo = [];
      this.openList = [];
    },

    computFGH( block, currentBlock, endBlock ){
      let { x, y, index } = block;
      let H = Math.abs( x - endBlock.x ) + Math.abs( y - endBlock.y );
      let G;
      if( !currentBlock.G ){
        G = Math.abs( x - this.startBlock.x ) + Math.abs( y - this.startBlock.y );
      }else{
        G = currentBlock.G + this.G;
      }
      let F = H + G;
      block.H = H;
      block.G = G;
      block.F = F;
      this.FGHBlocks.push( block );
    },

    pathScoring( startBlock, endBlock ){
      let { width, index, x, y, direction } = startBlock;
      for( let item of direction ){
        let borderUpon = this.modelMap.get( item );
        if( borderUpon && borderUpon.type !== 'roadblock' && borderUpon.type !== 'start' && borderUpon.type !== 'openList' ){
          this.computFGH( borderUpon, startBlock, endBlock );
          if( borderUpon.type !== 'end' ){
            borderUpon.type = 'openList';
          }
          borderUpon.parent = index;
          this.openList.push( borderUpon );
        }
      }
      this.openList.sort(( a, b ) => b.F - a.F );
    },

    clickSeting(){
      this.showModal = true;
      this.active = 'seting';
    },

    cancel(){
      this.active = null;
      this.showModal = false;
    },

    ok(){
      this.G = this.setG;
      this.rowNum = this.setRowNum;
      this.showFGH = this.setShowFGH;
      this.initData();
      this.drawMap();
      this.active = null;
      this.showModal = false;
    }
  }
}
</script>

