              text-align: right;
            }

            >input,
            >select{
              width: 80px;
              border: 1px solid #d7d9de;
              border-radius: .3rem;
            }

            >input{
              padding: 0 .5rem;
            }

            >select{
              width: 98px;
              padding: 0 .2rem;
            }
          }
        }

        >.modal-footer{
          display: flex;
          text-align: center;
          border-top: 1px solid #d7d9de;
          >div{
            width: 50%;
            padding: .6rem 0;
          }
          >div:first-child{
            border-right: 1px solid #d7d9de;
          }

          >div:last-child{
            color: #7ab0ee;
          }
        }
      }
    }
  }
}
